<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    /*position: relative;*/
    border-radius: 4px;
    /*overflow: hidden;*/
    height: 1000px;
  }
  .layout-breadcrumb{
    padding: 10px 15px 0;
  }
  .layout-content{
    min-height: 200px;
    margin: 15px;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
  }
  .layout-content-main{
    padding: 10px;
  }
  .layout-copy{
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
  }
  .layout-menu-left{
    background: #464c5b;
  }
  .layout-header{
    height: 60px;
    background: #fff;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
  }
  .layout-logo-left{
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
  }
  .layout-ceiling-main a{
    color: #9ba7b5;
  }
  .layout-hide-text .layout-text{
    display: none;
  }
  .ivu-col{
    transition: width .2s ease-in-out;
  }

  .ivu-icon+a{
    color: rgb(158, 167, 180);
  }
  .ivu-row-flex {
    height: 100%;
  }
</style>
<template>
  <div class="layout" :class="{'layout-hide-text': spanLeft < 5}">
    <Row type="flex">
      <i-col :span="spanLeft" class="layout-menu-left">
        <Menu :active-name="activeMenu" theme="dark" width="auto">
          <div class="layout-logo-left">
            <Select style="width:200px">
              <Option v-for="item in appList" :value="item.value" :key="item">{{ item.label }}</Option>
            </Select>

          </div>
          <Menu-item name="appMgr">
            <Icon type="ios-navigate" :size="iconSize"></Icon>
            <a class="layout-text" href="index.html">应用管理</a>
          </Menu-item>
          <Menu-item name="2">
            <Icon type="ios-keypad" :size="iconSize"></Icon>
            <span class="layout-text">用户管理</span>
          </Menu-item>
          <Menu-item name="roleMgr">
            <Icon type="ios-analytics" :size="iconSize"></Icon>
            <a class="layout-text" href="roleMgr.html">角色管理</a>
          </Menu-item>
          <Menu-item name="4">
            <Icon type="ios-analytics" :size="iconSize"></Icon>
            <span class="layout-text">菜单管理</span>
          </Menu-item>

        </Menu>
      </i-col>
      <i-col :span="spanRight">
        <div class="layout-header">
          <i-button type="text" @click="toggleClick">
            <Icon type="navicon" size="32"></Icon>
          </i-button>
        </div>
        <div class="layout-breadcrumb">
          <Breadcrumb>
            <Breadcrumb-item href="#">首页</Breadcrumb-item>
            <Breadcrumb-item href="#">应用中心</Breadcrumb-item>
            <Breadcrumb-item>某应用</Breadcrumb-item>
          </Breadcrumb>
        </div>
        <div class="layout-content">
          <div class="layout-content-main">
            <slot></slot>
          </div>
        </div>

      </i-col>
    </Row>
  </div>
</template>
<script>

  ///rest/configServer/access/appMgr
  import {DataSource} from "../utils/dataSource.js";

  export default {
    props: ['activeMenu'],
    data () {
    return {
      spanLeft: 5,
      spanRight: 19,
      appList:[]
    }
  },
  computed: {
    iconSize () {
      return this.spanLeft === 5 ? 14 : 24;
    }
  },
  methods: {
    toggleClick () {
      if (this.spanLeft === 5) {
        this.spanLeft = 2;
        this.spanRight = 22;
      } else {
        this.spanLeft = 5;
        this.spanRight = 19;
      }
    }
  },
    mounted: async function() {

      this.ds = new DataSource({
        queryUrl:'/rest/configServer/access/appMgr',
        name:'appEntity',
      });
      let response = await this.ds.paged();

      let data = await response.json();
      if(data.success) {
        this.appList = [{value:'11', label:'cc'}];
      }
    }
  }
</script>



